import React from "react"
import { HIs, HFormat } from "@macashipo/mlib"
import useConfigTableCell from "../../hooks/useConfigTableCell"
import { fnListMyTableCell } from "src/helpers/fnList"
import { FlowGetDateFormat, KMore } from "src/helpers/Flows/TableCell"
export default function MTCReadonly(props) {
  const { cell } = props
  const ctc = useConfigTableCell({ props })
  if (HIs.isObject(cell)) {
    return <div></div>
  }
  let _format = FlowGetDateFormat({ ...ctc })
  let _display = HFormat.format(cell, {
    type: "date",
    format: _format,
  })
  return (
    <div className="mct-readonly_date disabled" style={ctc.styleWrap()}>
      {_display}
    </div>
  )
}
